<template>
  <div
    class="PlayLyric"
    @click="$emit('updata:Img')"
    @touchstart="started"
    @touchend="ending"
  >
    <div class="box" @touchmove="moveing">
      <ul class="lyric">
        <li class="list" v-for="item in lyric" :key="item.time">
          {{ item.text }}
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  props: ["MusicId", "ispaused", "currentTime", "duration","listLyric"],
  data() {
    return {
      lyric: this.listLyric,
      clientX: 0,
      clientY: 0,
      moveBoolean: true,
      moveDown:0,
    };
  },
  created() {
    this.axios.get("/lyric?id=" + this.MusicId.id).then((datas) => {
      // console.log(datas.data.lrc.lyric);
      this.lyric = this.paresLyric(datas.data.lrc.lyric);
    });
  },
  methods: {
    paresLyric(lyric) {
      var patt = /\[\d{2}:\d{2}\.\d{2,3}\]/gi;
      var arr = lyric
        .split("\n")
        .filter((e) => e)
        .map((str) => {
          var time = str.match(patt)[0].replace(/(\[|\])/gi, "");
          var timeArr = time.split(":");
          time = Number(timeArr[0]) * 60 + Number(timeArr[1]);
          var text = str.replace(patt, "");
          return { time, text };
        });
      for (var j = 0; j < arr.length; j++) {
        // console.log('['+arr[j] .text+','+arr[j].time+']');
        if (arr[j].text == "") {
          arr[j].text = "";
        }
      }
      return arr;
    },
    started(e) {
      this.clientX = e.changedTouches[0].clientX;
      this.clientY = e.changedTouches[0].clientY;
    },
    ending(e) {
      var lastClientX = e.changedTouches[0].clientX;
      var abs = Math.abs(this.clientX - lastClientX);
      if (abs >= 180) {
        this.$emit("updata:Img");
      }
    },
    moveing(e) {
      var lastClientY = e.changedTouches[0].clientY;
      var movingDistance = lastClientY - this.clientY;
      console.log(this.moveDown + movingDistance);
      this.moveBoolean = false;
      var ul = document.querySelector(".lyric");
      ul.style.marginTop = this.moveDown + movingDistance + "px";
      // this.moveDown = this.moveDown + movingDistance;
      // this.moveBoolean = true;
    },
  },
  watch: {
    currentTime() {
      if (this.moveBoolean) {
        var ul = document.querySelector(".lyric");
        var list = document.querySelectorAll(".list");

        if (this.lyric == null) {
          return;
        } else {
          var i = this.lyric.findIndex((element) => {
            return element.time >= this.currentTime;
          });
          // console.log(i);
          // console.log(list);
          if (i == -1) {
            return;
          } else {
            this.moveDown =  -45 * (i-1) + 200;
            ul.style.marginTop = this.moveDown + "px";
            for (var j = 0; j < this.lyric.length; j++) {
              list[j].style.color = "#fff";
              list[j].style.transform = "scale(1,1)";
            }
            list[i-1].style.color = "#d43c33";
            list[i-1].style.transform = "scale(1.2,1.2)";
          }
        }
      }
    },
    MusicId(){
      this.axios.get("/lyric?id=" + this.MusicId.id).then((datas) => {
        // console.log(datas.data.lrc.lyric);
        this.lyric = this.paresLyric(datas.data.lrc.lyric);
      });
    }
  },
};
</script>

<style lang='less'>
.PlayLyric {
  width: 100%;
  box-sizing: border-box;
  height: 100vh;
  padding-bottom: 180px;
  position: relative;
  text-align: center;
  .box {
    margin-top: 20px;
    height: calc(100vh - 225px - 80px);
    overflow: hidden;
    .lyric {
      margin-top: 225px;
      line-height: 45px;
      display: block;
      border: none;
      transition: all 1s;
      color: #fff;
    }
  }
}
</style>