<template>
  <div class="PlayBottom">
    <div class="head">
      <span class="iconfont icon-shoucang"></span>
      <span class="iconfont icon-xiazai"></span>
      <span class="iconfont icon-huaban"></span>
      <span class="iconfont icon-pinglun"></span>
      <span class="iconfont icon-gengduo"></span>
    </div>
    <div class="body">
      <span>{{ newPlay }}</span>
      <div class="meter">
        <div class="big">
          <input
            type="range"
            min="0"
            :max="duration"
            step="1"
            @input="getValue"
            :value="currentTime"
          />
          <div class="small" :style="{ width: width + '%' }"></div>
          <div class="point" :style="{ left: width +(-1) +'%' }"></div>
        </div>
      </div>
      <span>{{ allPlay }}</span>
    </div>
    <div class="foot">
      <span :class="arr" @click="playMethod"></span>
      <span class="iconfont icon-shangyishoushangyige" @click="$emit('last')"></span>
      <template>
        <div @click='$emit("updata:ispaused")'>
          <span
            class="iconfont icon-bofang-yuanshijituantubiao"
            style="font-size: 70px"
            v-if="ispaused"
          ></span>
          <span
            class="iconfont icon-stop"
            style="font-size: 70px"
            v-else
          ></span>
        </div>
      </template>
      <span class="iconfont icon-xiayigexiayishou" @click="$emit('next')"></span>
      <span class="iconfont icon-bofangliebiao"></span>
    </div>
  </div>
</template>

<script>
export default {
  props: ["MusicId", "currentTime", "ispaused",'duration'],
  data() {
    return {
      flag:false,
      firstX:0,
      lastX:0,
      offsetLeft:0,
      arr:'iconfont icon-yuanxunhuanbofang',
      arrIndex:0,
    };
  },
  computed: {
    allPlay: function () {
      let minute = Math.floor(this.duration / 60);
      minute = minute < 10 ? "0" + minute : minute;
      let second = (Math.floor(this.duration % 60));
      second = second < 10 ? "0" + second : second;
      return minute + ":" + second;
    },
    newPlay: function () {
      return (
        (Math.floor(this.currentTime / 60) < 10
          ? "0" + Math.floor(this.currentTime / 60)
          : Math.floor(this.currentTime / 60)) +
        ":" +
        ((Math.floor(this.currentTime % 60)) < 10
          ? "0" + (Math.floor(this.currentTime % 60))
          : (Math.floor(this.currentTime % 60)))
      );
    },
    width() {
      return (this.currentTime / this.duration) * 100;
    },
  },
  methods: {
    getValue: function (e) {
      // console.log(e.target.value);
      this.$emit("update:currentTime", e.target.value);
    },
    playMethod(){
      var classArr = ['icon-yuanxunhuanbofang','icon-suijibofang','icon-hanhan-01-01'];
      this.arrIndex++;
      if(this.arrIndex==3){this.arrIndex=0;}
      this.arr = "iconfont " + classArr[this.arrIndex];
      this.$emit("updata:classArr",classArr[this.arrIndex]);
    }
  },
};
</script>

<style lang='less'>
.PlayBottom {
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 180px;
  padding: 0 20px;
  color: white;
  .head {
    height: 50px;
    width: 100%;
    display: flex;
    text-align: center;
    padding: 0 15px;
    span {
      flex: 1 1 auto;
      font-size: 25px;
    }
  }
  .body {
    height: 30px;
    width: 100%;
    display: flex;
    align-items: center;
    position: relative;
    .meter {
      flex: 1 1 auto;
      display: flex;
      align-items: center;
      padding: 0 15px;
      .big {
        width: 100%;
        height: 5px;
        background-color: gray;
        border-radius: 10px;
        position: relative;
        input{
          position: absolute;
          width: 100%;
          left: 0;
          height: 5px;
          z-index: 99;
          opacity: 0;
        }
        .small {
          position: absolute;
          left: 0;
          width: 0;
          height: 5px;
          background-color: white;
          border-radius: 10px;
        }
        .point {
          position: absolute;
          left: 0px;
          top: -3px;
          width: 10px;
          height: 10px;
          background-color: white;
          border-radius: 50%;
        }
      }
    }
  }
  .foot {
    height: 100px;
    width: 100%;
    display: flex;
    align-items: center;
    text-align: center;
    span {
      flex: 1 1 auto;
      font-size: 25px;
    }
  }
}
</style>