<template>
  <div>
    <audio
      :src="
        'https://music.163.com/song/media/outer/url?id=' + MusicId.id + '.mp3'
      "
      autoplay
      controls
      v-show="isHave"
      ref="audio"
    ></audio>
    <transition
      name="custom-classes-transition"
      enter-active-class="animate__animated animate__slideInUp"
      leave-active-class="animate__animated animate__slideOutDown"
    >
      <div class="play" v-show="isShowPlay" @click="isShowPlay = false">
        <div class="left">
          <div class="img">
            <img :src="MusicId.picUrl" :class="{ paused: ispaused }" />
          </div>
          <div class="sq">
            <p>{{ MusicId.name }}</p>
            <span
              v-for="(itemName, index) in MusicId.song.artists"
              :key="itemName.id"
            >
              <span v-if="index"> / </span>{{ itemName.name }}
            </span>
          </div>
        </div>
        <div class="right" @click.stop="togglePlayState">
          <canvas ref="circle" width="50" height="50"></canvas>
          <span class="iconfont icon-z" v-if="ispaused"></span>
          <span class="iconfont icon-zanting" v-else></span>
        </div>
      </div>
    </transition>

    <transition
      name="custom-classes-transition"
      enter-active-class="animate__animated animate__fadeIn"
      leave-active-class="animate__animated animate__fadeOutDown"
    >
      <div class="playbig" v-if="!isShowPlay">
        <div
          class="mask"
          :style="{ backgroundImage: `url('${MusicId.picUrl}')` }"
        ></div>
        <PlayDetails
          @show-play-big="isShowPlay = true"
          :MusicId="MusicId"
          :currentTime="currentTime"
          :ispaused="ispaused"
          @updata:ispaused="togglePlayState"
          :duration="duration"
          @update:currentTime="$refs.audio.currentTime = $event"
          @last="Lasting"
          @next="Nexting"
          @updata:classArr="classArrToggle"
          :listLyric="listLyric"
        />
      </div>
    </transition>
  </div>
</template>

<script>
import PlayDetails from "@/components/PlayDetails.vue";

export default {
  components: {
    PlayDetails,
  },
  props: ["MusicId", "paused", "name", "currentIndex", "playlist"],
  data() {
    return {
      isHave: false,
      duration: 0,
      currentTime: 0,
      isShowPlay: true,
      ispaused: false,
      classNow: "icon-yuanxunhuanbofang",
      listLyric: [],
    };
  },
  watch: {
    MusicId: function () {
      this.axios.get("/song/url?id=" + this.MusicId.id).then((datas) => {
        // console.log(datas);
        return datas;
      });
      this.axios.get("/lyric?id=" + this.MusicId.id).then((datas) => {
        this.listLyric = this.paresLyric(datas.data.lrc.lyric);
      });
    },
  },
  methods: {
    paresLyric(lyric) {
      var patt = /\[\d{2}:\d{2}\.\d{2,3}\]/gi;
      var arr = lyric
        .split("\n")
        .filter((e) => e)
        .map((str) => {
          var time = str.match(patt)[0].replace(/(\[|\])/gi, "");
          var timeArr = time.split(":");
          time = Number(timeArr[0]) * 60 + Number(timeArr[1]);
          var text = str.replace(patt, "");
          return { time, text };
        });
      for (var j = 0; j < arr.length; j++) {
        // console.log('['+arr[j] .text+','+arr[j].time+']');
        if (arr[j].text == "") {
          arr[j].text = "";
        }
      }
      return arr;
    },
    drawCircle: function (n, total) {
      let canvas = this.$refs.circle;
      let ctx = canvas.getContext("2d");
      ctx.clearRect(0, 0, 50, 50);

      ctx.beginPath();
      ctx.strokeStyle = "rgba(0, 0, 0, 0.2)";
      ctx.arc(25, 25, 16, 0, Math.PI * 2, false);
      ctx.stroke();
      ctx.closePath();

      ctx.beginPath();
      ctx.strokeStyle = "red";
      ctx.arc(
        25,
        25,
        16,
        Math.PI * -0.5,
        Math.PI * ((n / total) * 2 - 0.5),
        false
      );
      ctx.liaeWidth = 4;
      ctx.stroke();
      ctx.closePath();
    },
    togglePlayState: function () {
      let audio = this.$refs.audio;
      // this.ispaused=!this.ispaused;
      if (this.ispaused) {
        audio.play();
      } else {
        audio.pause();
      }
    },
    // 
    calculateNext: function () {
      //     
      let nextIndex;
      if (this.currentIndex < this.playlist.length - 1) {
        nextIndex = this.currentIndex + 1;
      } else {
        nextIndex = 0;
      }
      return nextIndex;
    },
    calculateLast: function () {
      //     
      let nextIndex;
      if (this.currentIndex > 0) {
        nextIndex = this.currentIndex - 1;
      } else {
        nextIndex = this.playlist.length - 1;
      }
      return nextIndex;
    },
    playNext: function () {
      console.log("");
      let index = this.calculateNext();

      this.$emit("update:music", {
        item: this.playlist[index],
        index,
      });
    },
    playLast: function () {
      let index = this.calculateLast();
      // .song.artists
      this.$emit("update:music", {
        item: this.playlist[index],
        index,
      });
    },
    // 
    playSuiJiNext: function () {
      let nextIndex = Math.floor(Math.random() * this.playlist.length);
      this.$emit("update:music", {
        item: this.playlist[nextIndex],
        nextIndex,
      });
    },
    classArrToggle(e) {
      if (e == "icon-shunxubofang") {
        this.classNow = e;
      } else if (e == "icon-suijibofang") {
        this.classNow = e;
      } else if (e == "icon-hanhan-01-01") {
        this.classNow = e;
      } else if (e == "icon-yuanxunhuanbofang") {
        this.classNow = e;
      }
    },
    // 
    Nexting() {
      var audio = this.$refs.audio;
      if (this.classNow == "icon-yuanxunhuanbofang") {
        this.playNext();
      } else if (this.classNow == "icon-hanhan-01-01") {
        audio.load();
      } else if (this.classNow == "icon-suijibofang") {
        this.playSuiJiNext();
      }
    },
    // 
    Lasting() {
      var audio = this.$refs.audio;
      if (this.classNow == "icon-yuanxunhuanbofang") {
        this.playNext();
      } else if (this.classNow == "icon-hanhan-01-01") {
        audio.load();
      } else if (this.classNow == "icon-suijibofang") {
        this.playSuiJiNext();
      }
    },
  },
  mounted() {
    var audio = this.$refs.audio;
    audio.addEventListener("pause", () => {
      console.log("");
      this.ispaused = true;
      this.$emit("updata-pause", this.ispaused);
      // this.$emit("@updata:paused",true);
    });
    audio.addEventListener("play", () => {
      console.log("");
      this.ispaused = false;
      this.$emit("updata-pause", this.ispaused);
      // this.$emit("@updata:paused",false);
    });

    audio.addEventListener("durationchange", () => {
      this.duration = audio.duration;
      // console.log("", this.duration);
    });

    audio.addEventListener("timeupdate", () => {
      this.currentTime = audio.currentTime;
      // console.log("", this.currentTime);

      this.drawCircle(this.currentTime, this.duration);
    });
    audio.addEventListener("ended", () => {
      if (this.classNow == "icon-yuanxunhuanbofang") {
        this.playNext();
      } else if (this.classNow == "icon-hanhan-01-01") {
        audio.load();
      } else if (this.classNow == "icon-suijibofang") {
        this.playSuiJiNext();
      }
    });
  },
};
</script>

<style lang='less'>
.play {
  position: fixed;
  bottom: 0;
  left: 0;
  border-top: 1px solid rgba(0, 0, 0, 0.1);
  width: 100%;
  height: 50px;
  background: white;
  display: flex;
  .left {
    flex: 1 1 auto;
    width: 0;
    display: flex;
    .img {
      width: 49px;
      height: 49px;
      img {
        margin: 7px;
        width: 35px;
        height: 35px;
        border-radius: 50%;
        animation: trans 3s linear infinite;
        &.paused {
          animation-play-state: paused;
        }
      }
    }
    .sq {
      margin-left: 12px;
      margin-top: 3px;
      overflow: hidden;
      & > p {
        font-size: 16px;
        margin-top: 3px;
        text-overflow: ellipsis;
        white-space: nowrap;
        overflow: hidden;
      }
      span {
        margin-top: 3px;
        font-size: 12px;
        color: gray;
      }
    }
  }
  .right {
    height: 100%;
    span {
      position: absolute;
      top: 14px;
      right: 14px;
      width: 22px;
      height: 22px;
      font-size: 22px;
    }
  }
}
.playbig {
  width: 100vw;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 9;
  background: linear-gradient(
    to right,
    rgb(56, 56, 56),
    rgb(95, 95, 95),
    rgb(56, 56, 56)
  );
  .mask {
    background-size: cover;
    background-position: center;
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;
    filter: blur(8px) brightness(0.5);
  }
}
@keyframes trans {
  form {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
</style>