<template>
  <div class="LeftNumberList">
    <div
      class="items"
      v-for="(item, index) in NewMusicList"
      :key="item.id"
      @click="
        $emit('updata:music', {
          ...item,
          picUrl: item.al.picUrl,
          song: { artists: item.ar },
          id: item.id,
          name: item.name,
          index:index
        });
        playlist()
      "
    >
      <!-- @click="$emit('updata:music', item)" -->
      <!-- {picUrl:item.al.picUrl,song:{artists:item.ar},id:item.id,name:item.name} -->
      <div class="h">
        <h3 v-if="index < 3" :style="{ color: red }">0{{ index + 1 }}</h3>
        <h3 v-if="index >= 3 && index < 9">0{{ index + 1 }}</h3>
        <h3 v-if="index >= 9">{{ index + 1 }}</h3>
      </div>
      <div class="left">
        <p>{{ item.name }}</p>
        <div class="sq">
          <span v-for="(itemName, index) in item.ar" :key="itemName.id"
            ><span v-if="index"> / </span>{{ itemName.name }}</span
          ><template
            ><span> - {{ item.name }}</span>
          </template>
        </div>
      </div>
      <div class="right">
        <span v-if="item.id != MusicId.id"></span>
        <span v-else :class="{ active: true, paused: !paused }">
          <i></i>
          <i></i>
          <i></i>
          <i></i>
        </span>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: ["NewMusicList", "paused", "MusicId"],
  data() {
    return {
      red: "red",
    };
  },
  methods: {
    playlist(){
      var MusicList = this.NewMusicList.map(function(item){
        return {...item,picUrl: item.al.picUrl,
          song: { artists: item.ar },
          id: item.id,
          name: item.name,}
      });
      this.$emit('updata:playlist', MusicList);
      // console.log(MusicList);
    }
  },
};
</script>

<style lang='less'>
.items {
  display: flex;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  border-bottom: 1px solid rgba(0, 0, 0, 0.08);
  .h {
    width: 40px;
    margin-left: -10px;
    h3 {
      text-align: center;
      line-height: 55px;
    }
  }
  .left {
    flex: 1 1 auto;
    padding: 6px 0;
    width: 0;
    p {
      white-space: nowrap;
      text-overflow: ellipsis;
      overflow: hidden;
      height: 24px;
      font-size: 17px;
    }
    .sq {
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
      word-break: normal;
      span {
        font-size: 12px;
        color: #888;
        text-overflow: ellipsis;
        overflow: hidden;
        word-break: normal;
      }
    }
    .sq::before {
      content: "";
      background: no-repeat 0 0
        url("https://s3.music.126.net/mobile-new/img/index_icon_2x.png?5207a28c3767992ca4bb6d4887c74880=");
      width: 12px;
      height: 8px;
      margin-right: 4px;
      display: inline-block;
      background-size: 166px 97px;
    }
  }
  .right {
    padding: 0 10px;
    height: 54px;
    display: flex;
    align-items: center;
    span {
      width: 22px;
      height: 22px;
      display: flex;
      background: no-repeat -24px 0 url("https://s3.music.126.net/mobile-new/img/index_icon_2x.png?5207a28c3767992ca4bb6d4887c74880=");
      background-size: 166px 97px;
      justify-content: space-between;
      align-items: flex-end;
    }
    span.active {
      background: none;
      i {
        width: 5px;
        height: 30px;
        background: red;
        transform-origin: center bottom;
        animation: playing 0.6s linear -0.2s infinite alternate-reverse;
        &:first-of-type {
          animation-delay: 0s;
        }
        &:nth-child(2) {
          animation-delay: 0.2s;
        }
        &:last-of-type {
          animation-delay: -0.4s;
        }
      }
    }
    span.paused {
      i {
        animation-play-state: paused;
      }
    }
  }
}
@keyframes playing {
  from {
    // height: 50px;
    transform: scaleY(1);
  }
  to {
    // height: 10px;
    transform: scaleY(0.2);
  }
}
</style>