<template>
  <div class="search animate__animated animate__fadeIn">
    <div class="input">
      <div class="small">
        <span class="iconfont icon-sousuo"></span>
        <input
          type="text"
          placeholder=""
          v-model="inputValue"
          @keyup.enter="creatHistory"
        />
        <div
          class="close"
          v-if="inputing"
          @click="(inputValue = ''), (playAllSearchWord = '')"
        >
          <span class="iconfont icon-shanchu"></span>
        </div>
      </div>
    </div>

    <div class="one" v-if="!inputing">
      <div class="hot-search">
        <p></p>
        <ul class="hsearch">
          <li
            v-for="(item, index) in hotSearch"
            :key="index"
            @click="creatPlay(item.first)"
          >
            {{ item.first }}
          </li>
        </ul>
      </div>

      <div class="history">
        <ul class="history-ul">
          <li
            v-for="(item, index) in historyItem"
            :key="index"
            @click="creatPlay(item)"
          >
            <span class="iconfont icon-shijian"></span>
            <strong>{{ item }}</strong>
            <span class="iconfont icon-quxiao" @click.stop="deletes"></span>
          </li>
        </ul>
      </div>
    </div>

    <div class="two" v-if="inputing && !playAllSearchWord">
      <h3>"{{ inputValue }}"</h3>
      <ul class="two-ul">
        <li v-for="item in SearchResult" :key="item.id" @click="creatPlay(item.keyword)">
          <i class="iconfont icon-sousuo"></i><span>{{ item.keyword }}</span>
        </li>
      </ul>
    </div>

    <div class="three" v-if="inputing && playAllSearchWord">
      <NewMusic
        :head="head"
        :paused="$attrs.paused"
        :MusicId="$attrs.MusicId"
        :NewMusicList="playAllSearch"
        :search="search"
        @updata:music="$emit('updata:music', $event)"
        @updata:playlist="$emit('updata:playlist', $event)"
        :isShow="$attrs.isShow"
      ></NewMusic>
    </div>
  </div>
</template>

<script>
import NewMusic from "@/components/NewMusic.vue";

export default {
  components: {
    NewMusic,
  },
  data() {
    return {
      hotSearch: [],
      inputValue: "",
      SearchResult: [],
      inputing: false,
      playAllSearchWord: "",
      playAllSearch: [],
      historyItem: [],
      head: false,
      NewMusicList: [],
      search: true,
    };
  },
  created() {
    this.axios.get("/search/hot").then((datas) => {
      this.hotSearch = datas.data.result.hots;
    });
  },
  watch: {
    inputValue(n) {
      if (n) {
        this.inputing = true;
        this.axios
          .get("/search/suggest?keywords=" + n + "&type=mobile")
          .then((datas) => {
            this.SearchResult = datas.data.result.allMatch;
          });
      } else {
        this.SearchResult = [];
        this.inputing = false;
      }
    },
    playAllSearchWord(n) {
      this.axios.get("/search?keywords=" + n).then((datas) => {
        this.playAllSearch = datas.data.result.songs;
      });
    },
  },
  methods: {
    deletes(e) {
      e.target.parentNode.remove();
    },
    // 
    creatHistory() {
      console.log(this.inputValue);
      this.playAllSearchWord = this.inputValue;
      if (this.historyItem.indexOf(this.inputValue) == -1) {
        this.historyItem.unshift(this.inputValue);
      }
    },
    // 
    creatPlay($event) {
      if (this.historyItem.indexOf($event) == -1) {
        this.historyItem.unshift($event);
      } else {
        this.historyItem.splice(this.historyItem.indexOf($event), 1);
        this.historyItem.unshift($event);
      }
      this.inputValue = $event;
      this.playAllSearchWord = $event;
    },
  },
};
</script>

<style scoped lang='less'>
.search {
  width: 100%;
  .input {
    width: 100%;
    padding: 15px 10px;
    // background: brown;
    .small {
      padding: 0 30px;
      background: white;
      border-radius: 20px;
      position: relative;
      background: #ebecec;
      input {
        width: 100%;
        height: 30px;
        border: none;
        outline: none;
        background: #ebecec;
      }
      input::-webkit-input-placeholder {
        color: #c9c9c9;
      }
      input::-moz-input-placeholder {
        color: #c9c9c9;
      }
      input::-ms-input-placeholder {
        color: #c9c9c9;
      }
      & > span {
        position: absolute;
        left: 10px;
        top: 50%;
        margin-top: -9px;
        color: #c9c9c9;
      }
      .close {
        position: absolute;
        right: 10px;
        top: 50%;
        margin-top: -9px;
        color: #c9c9c9;
      }
    }
  }
  .hot-search {
    padding: 15px 10px 0;
    &::after {
      content: "";
      display: block;
      clear: both;
      line-height: 0;
      width: 0;
      height: 0;
    }
    p {
      font-size: 12px;
      line-height: 12px;
      color: #666;
    }
    .hsearch {
      margin: 10px 0 7px;
      display: block;
      li {
        float: left;
        height: 32px;
        margin-right: 8px;
        margin-bottom: 8px;
        padding: 0 14px;
        font-size: 14px;
        color: #333;
        line-height: 32px;
        border: 1px solid #d3d4da;
        border-radius: 32px;
      }
    }
  }
  .history {
    .history-ul {
      display: block;
      li {
        text-align: left;
        height: 45px;
        display: flex;
        align-items: center;
        span:nth-of-type(1) {
          margin: 0 10px;
          font-size: 16px;
        }
        span:nth-of-type(2) {
          margin: 0 10px;
          font-size: 32px;
        }
        strong {
          flex: 1 1 auto;
          font-weight: normal;
        }
      }
    }
  }
  .two {
    h3 {
      color: #507daf;
      height: 50px;
      line-height: 50px;
      margin-left: 10px;
      padding-right: 10px;
      font-size: 15px;
    }
    ul {
      display: block;
      li {
        margin-left: 10px;
        text-align: left;
        display: flex;
        align-items: center;
        height: 45px;
        i {
          font-size: 14px;
          margin-right: 7px;
          flex: 0 0 auto;
          color: #999;
        }
        span {
          flex: 1;
          display: inline-block;
          padding-right: 10px;
          height: 45px;
          line-height: 45px;
          border-bottom: 1px solid rgba(0, 0, 0, 0.1);
        }
      }
    }
  }
}
</style>