<template>
  <div class="RecommendPlayList">
    <div
      class="ListItem"
      v-for="item in playlist"
      :key="item.id"
      @click="jump(item.id)"
    >
      <div class="img">
        <img :src="item.picUrl" alt="" />
        <span>{{ item.playCount | toFixedCount }}</span>
      </div>
      <h6>{{ item.name }}</h6>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    playlist: {
      type: Array,
    },
  },
  filters: {
    toFixedCount: function (value) {
      return (value / 10000).toFixed(1) + "";
    },
  },
  methods: {
    jump: function (id) {
      this.$router.push({
        path: "/PlayListItem",
        query: {
          id,
        },
      });
    },
  },
};
</script>

<style lang='less'>
.RecommendPlayList {
  display: flex;
  flex-wrap: wrap;
  .ListItem {
    flex: 1 1 33.33vw;
    padding: 0 2px;
    margin-bottom: 16px;
    .img {
      position: relative;
      img {
        width: 100%;
      }
      span {
        position: absolute;
        right: 0;
        top: 0;
        color: white;
        text-shadow: 0 0 1px black;
        display: flex;
        align-items: center;
        font-size: 14px;
        &::before {
          content: "";
          background: no-repeat
            url("data:image/svg+xml;base64,");
          height: 14px;
          width: 14px;
          display: inline-block;
          vertical-align: middle;
          margin-right: 3px;
        }
      }
    }
    h6 {
      display: -webkit-box;
      -webkit-box-orient: vertical;
      -webkit-line-clamp: 2;
      overflow: hidden;
      text-overflow: ellipsis;
      padding: 6px 2px 0 6px;
      min-height: 42px;
    }
  }
}
</style>