<template>
  <div class="PlayListItem animate__animated animate__fadeIn" @touchstart='start' @touchend='ended'>
    <div class="list-top">
      <div class="bg-mack"></div>
      <div class="bg">
        <img :src="playlistInformation.coverImgUrl" />
      </div>
      <div class="plhead_wrap">
        <div class="left">
          <img :src="playlistInformation.coverImgUrl" />
          <span></span>
          <b>{{ playlistInformation.playCount | toFixedCount }}</b>
        </div>
        <div class="right">
          <h3>{{ playlistInformation.name }}</h3>
          <div class="tou">
            <img :src="playlistInformation.creator.avatarUrl" alt="" />
            <span>{{ playlistInformation.creator.nickname }}</span>
          </div>
        </div>
      </div>
    </div>
    <div class="font"></div>
    <LeftNumberList
      :paused="$attrs.paused"
      :MusicId="$attrs.MusicId"
      :NewMusicList="playSongs"
      @updata:music="$emit('updata:music', $event)"
      @updata:playlist="$emit('updata:playlist', $event)"
    ></LeftNumberList>
  </div>
</template>

<script>
import LeftNumberList from "@/components/LeftNumberList.vue";
export default {
  components: {
    LeftNumberList,
  },
  // avatarUrl
  props: [],
  data() {
    return {
      id: null,
      playlistInformation: {creator:{avatarUrl:'http://p1.music.126.net/GvUp9ev0x-LsSSLRvijytA==/109951165538510998.jpg'}},
      playlist: [],
      playSongs: [],
      move:false,
      clientX:0,
    };
  },
  filters: {
    toFixedCount: function (value) {
      return (value / 10000).toFixed(1) + "";
    },
  },
  created() {
    this.id = this.$route.query.id;
    var newplaylist = [];
    this.axios.get("/playlist/detail?id=" + this.id).then((datas) => {
      // console.log(datas.data.playlist);
      this.playlistInformation = datas.data.playlist;
      newplaylist = this.playlistInformation.trackIds.slice(0, 15);

      this.playlist = newplaylist.map(function (item) {
        return item.id;
      });
    });
  },
  beforeRouteUpdate(to, from, next) {
    this.id = to.query.id;
    var newplaylist = [];
    this.axios.get("/playlist/detail?id=" + this.id).then((datas) => {
      // console.log(datas.data.playlist);
      this.playlistInformation = datas.data.playlist;
      newplaylist = this.playlistInformation.trackIds.slice(0, 15);

      this.playlist = newplaylist.map(function (item) {
        return item.id;
      });
    });
    next();
  },
  activated() {
    this.id = this.$route.query.id;
    var newplaylist = [];
    this.axios.get("/playlist/detail?id=" + this.id).then((datas) => {
      // console.log(datas.data.playlist);
      this.playlistInformation = datas.data.playlist;
      newplaylist = this.playlistInformation.trackIds.slice(0, 15);

      this.playlist = newplaylist.map(function (item) {
        return item.id;
      });
    });
  },
  watch: {
    id: function () {
      this.id = this.$route.query.id;
      var newplaylist = [];
      this.axios.get("/playlist/detail?id=" + this.id).then((datas) => {
        // console.log(datas.data.playlist);
        this.playlistInformation = datas.data.playlist;
        newplaylist = this.playlistInformation.trackIds.slice(0, 15);

        this.playlist = newplaylist.map(function (item) {
          return item.id;
        });
      });
    },
    playlist() {
      var strings = this.playlist.join();
      this.axios.get("/song/detail?ids=" + strings).then((datas) => {
        this.playSongs = datas.data.songs;
      });
    },
  },
  methods: {
    start(e){
      this.clientX = e.changedTouches[0].clientX;
    },
    ended(e){
      var lastClientX = e.changedTouches[0].clientX;
      if(this.clientX+100<lastClientX){
        this.$router.push({path:'/'})
      }
    }
  },
};
</script>

<style lang='less'>
.PlayListItem {
  .list-top {
    width: 100%;
    height: 186px;
    padding: 30px 0px;
    position: absolute;
    overflow: hidden;
    top: 0;
    .bg-mack {
      width: 100%;
      height: 186px;
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      overflow: hidden;
      background: white;
    }
    .bg {
      width: 100%;
      height: 186px;
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      overflow: hidden;
      background: rgba(0, 0, 0, 0.25);
      & > img {
        float: left;
        width: 100%;
        filter: blur(20px);
      }
    }
    .plhead_wrap {
      display: flex;
      width: 100%;
      height: 126px;
      position: absolute;
      left: 15px;
      z-index: 2;
      .left {
        width: 126px;
        height: 126px;
        position: relative;
        img {
          min-width: 126px;
          height: 126px;
        }
        span {
          position: absolute;
          top: 10px;
          z-index: 3;
          left: 0;
          padding: 0 8px;
          height: 17px;
          font-size: 9px;
          color: #fff;
          background: rgba(217, 48, 48, 0.8);
          border-top-right-radius: 17px;
          border-bottom-right-radius: 17px;
        }
        b {
          position: absolute;
          top: 0;
          right: 2px;
          z-index: 3;
          padding-left: 15px;
          color: #fff;
          font-size: 12px;
          font-weight: normal;
          &::before {
            content: "";
            background: url("data:image/svg+xml;base64,")
              no-repeat;
            width: 11px;
            height: 10px;
            display: inline-block;
            padding-left: 15px;
            vertical-align: middle;
            margin-top: -2px;
          }
        }
      }
      .right {
        flex: 1 1 auto;
        margin-left: 16px;
        h3 {
          margin-right: 25px;
          color: #fefefe;
          font-size: 17px;
          height: 44px;
          line-height: 1.3;
        }
        .tou {
          margin-top: 20px;
          width: 250px;
          height: 30px;
          img {
            width: 30px;
            height: 30px;
            border-radius: 50%;
            vertical-align: middle;
            margin-right: 5px;
            display: inline-block;
          }
          span {
            font-size: 14px;
            color: hsla(0, 0%, 100%, 0.7);
          }
        }
      }
    }
  }
  .font {
    height: 23px;
    line-height: 23px;
    font-size: 12px;
    color: #666;
    padding: 0 10px;
    background: #eeeff0;
    margin-top: 62px;
  }
}
</style>