<template>
  <div>
    <PlayHead
      @show-play-big="$emit('show-play-big')"
      :MusicId="$attrs.MusicId"
    ></PlayHead>
    <transition
      name="custom-classes-transition"
      enter-active-class="animate__animated animate__fadeIn"
      leave-active-class="animate__animated animate__bounceOut"
    >
      <PlayCenterImg
        :MusicId="$attrs.MusicId"
        :ispaused="$attrs.ispaused"
        @updata:Img="toggle"
        v-if="Img"
      ></PlayCenterImg>
    </transition>
    <transition
      name="custom-classes-transition"
      enter-active-class="animate__animated animate__fadeIn"
    >
      <PlayLyric
        v-if="!Img"
        @updata:Img="toggle"
        :MusicId="$attrs.MusicId"
        :ispaused="$attrs.ispaused"
        :currentTime="$attrs.currentTime"
        :duration="$attrs.duration"
        :listLyric="$attrs.listLyric"
      ></PlayLyric>
    </transition>
    <PlayBottom
      :MusicId="$attrs.MusicId"
      :currentTime="$attrs.currentTime"
      :duration="$attrs.duration"
      :ispaused="$attrs.ispaused"
      @updata:ispaused="$emit('updata:ispaused')"
      @update:currentTime="$emit('update:currentTime', $event)"
      @last="$emit('last')"
      @next="$emit('next')"
      @updata:classArr="$emit('updata:classArr',$event)"
    ></PlayBottom>
  </div>
</template>

<script>
import PlayHead from "@/components/PlayHead.vue";
import PlayCenterImg from "@/components/PlayCenterImg.vue";
import PlayBottom from "@/components/PlayBottom.vue";
import PlayLyric from "@/components/PlayLyric.vue";

export default {
  components: {
    PlayHead,
    PlayCenterImg,
    PlayBottom,
    PlayLyric,
  },
  data() {
    return {
      Img: true,
    };
  },
  methods: {
    toggle() {
      this.Img = !this.Img;
    },
  },
};
</script>

