<template>
  <div class="recommend">
    <RandTl></RandTl>
    <RecommendPlayList :playlist='RecommendPlayList'/>
  </div>
</template>

<script>
import RandTl from "@/components/RendTl.vue";
import RecommendPlayList from "@/components/RecommendPlayList.vue";

export default {
  components: {
    RandTl,
    RecommendPlayList,
  },
  data() {
    return {
      RecommendPlayList: null,
    };
  },
  created() {
    this.axios.get("/personalized").then((datas) => {
      this.RecommendPlayList = datas.data.result.slice(0, 6);
      // console.log(this.RecommendPlayList);
    });
  },
};
</script>

