<template>
  <div class="PlayCenterImg" @click='$emit("updata:Img")' @touchstart='start' @touchend='ended'>
    <div class="circle">
      <div class="bigcircle" :class="{active:true,paused:ispaused}">
        <div class="smallcircle">
          <img :src="MusicId.picUrl" alt="" />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: ["MusicId","ispaused"],
  data() {
    return {
      clientX:0,
    }
  },
  methods: {
    start(e){
      this.clientX = e.changedTouches[0].clientX;
    },
    ended(e){
      var lastClientX = e.changedTouches[0].clientX;
      var abs = Math.abs(this.clientX-lastClientX);
      if(abs>=150){
        this.$emit('updata:Img');
      }
    }
  },
};
</script>

<style lang='less'>
.PlayCenterImg {
  width: 100%;
  box-sizing: border-box;
  height: 100vh;
  padding-bottom: 180px;
  position: relative;
  .bigcircle {
    &.active{
      animation: trans 5s linear infinite;
    }
    &.paused{
      animation-play-state: paused;
    }
    position: absolute;
    top: 50%;
    left: 50%;
    margin-top: -300px;
    margin-left: -140px;
    width: 280px;
    height: 280px;
    background: url("//s3.music.126.net/mobile-new/img/disc-plus.png?b700b62e1971b351dcb8b8ce1c9ceea3=")
      no-repeat;
    background-size: 280px 280px;
    &::before {
      content: "";
      width: 286px;
      height: 286px;
      display: block;
      // margin-top: 10px;
      margin-left: -3px;
      background: url("http://s3.music.126.net/mobile-new/img/disc_light.png?2bb24f3fd11569809b817b4988f12bc7=")
        no-repeat;
      background-size: contain;
    }
    .smallcircle {
      width: 150px;
      height: 150px;
      margin-left: 25px;
      margin-top: 25px;
      img {
        width: 180px;
        height: 180px;
        background: rgba(255, 255, 255, 0.5);
        display: block;
        border-radius: 50%;
        position: absolute;
        top: 50%;
        left: 50%;
        margin-top: -90px;
        margin-left: -90px;
      }
    }
  }
}
@keyframes trans {
  form {
    transform: rotate(0deg);
    transform-origin: center center;
  }
  to {
    transform: rotate(360deg);
    transform-origin: center center;
  }
}
</style>